        <option>One</option>
        <option>Compare</option>
      </select>
    </div>

    <div v-if="moodImput === 'Compare'" >
      <div class="select-mood">
        <select v-model="mood">
          <option>Anger</option>
          <option>Anticipation</option>
          <option>Joy</option>
          <option>Trust</option>
          <option>Fear</option>
          <option>Surprise</option>
          <option>Sadness</option>
          <option>Disgust</option>
        </select>
        <p v-if="mood === 'Anger'">To fight against problems - annoyance, anger, rage</p>
        <p v-if="mood === 'Anticipation'">To look forward to a plan - interest, anticipation, vigilance</p>
        <p v-if="mood === 'Joy'">To remind us what's important - serenity, joy, ecstasy</p>
        <p v-if="mood === 'Trust'">To connect with peolpe that help - acceptance, trust, admiration</p>
        <p v-if="mood === 'Fear'">To protect us from danger - apprehension, trust, admiration</p>
        <p v-if="mood === 'Surprise'">To focus us on new situations - distraction, surprise, amazement</p>
        <p v-if="mood === 'Sadness'">To connect us with those we love - pensiveness, sadness, grief</p>
        <p v-if="mood === 'Disgust'">To reject what is unhealthy - boredom, disgust, loathing</p>
      </div>

      <div class="movieContainer" v-if="movie1._id">
        <a @click.stop="getRandomMovie1">refresh</a>
        <poster :key="movie1._id" class="moodPoster" :poster="movie1.moodPoster" />
        <h3>{{movie1.Title}}</h3>
        <p>{{movie1.Released}}</p>
        <p>{{movie1.Genre}}</p>
        <p>{{movie1.Actors}}</p>
      </div>

      <div class="movieContainer" v-if="movie2._id">
        <a @click.stop="getRandomMovie2">refresh</a>
        <poster v-if="movie2" :key="movie2._id" class="moodPoster" :poster="movie2.moodPoster" />
        <h3>{{movie2.Title}}</h3>
        <p>{{movie2.Released}}</p>
        <p>{{movie2.Genre}}</p>
        <p>{{movie2.Actors}}</p>
      </div>
    </div>



    <div v-if="moodImput === 'One'" >
      <div class="movieContainerOne" v-if="movie1._id">
          <a @click.stop="getRandomMovie1">refresh</a>
          <poster :key="movie1._id" class="moodPoster" :poster="movie1.moodPoster" />
          <h3>{{movie1.Title}}</h3>
          <p>{{movie1.Released}}</p>
          <p>{{movie1.Genre}}</p>
          <p>{{movie1.Actors}}</p>
      </div>
      
      <div class="moodSliders" v-if="movie1._id">
        
        <input class="slider" type="range" id="anger" min="0" max="100" step="1" v-model="movie1.Anger" />
        <p><label for="anger">Anger {{movie1.Anger}}</label> To fight against problems - annoyance, anger, rage</p>

        
        <input class="slider" type="range" id="anticipation" min="0" max="100" step="1" v-model="movie1.Anticipation" />        
        <p><label for="anticipation">Anticipation {{movie1.Anticipation}}</label> To look forward to a plan - interest, anticipation, vigilance</p>

        <input class="slider" type="range" id="joy" min="0" max="100" step="1" v-model="movie1.Joy" />
        <p><label for="joy">Joy {{movie1.Joy}}</label> To remind us what's important - serenity, joy, ecstasy</p>

        <input class="slider" type="range" id="trust" min="0" max="100" step="1" v-model="movie1.Trust" />
        <p><label for="trust">Trust {{movie1.Trust}}</label> To connect with peolpe that help - acceptance, trust, admiration</p>
        
        <input class="slider" type="range" id="fear" min="0" max="100" step="1" v-model="movie1.Fear" />
        <p><label for="fear">Fear {{movie1.Fear}}</label> To protect us from danger - apprehension, fear, terror</p>

        <input class="slider" type="range" id="surprise" min="0" max="100" step="1" v-model="movie1.Surprise" />
        <p><label for="surprise">Surprise {{movie1.Surprise}}</label> To focus us on new situations - distraction, surprise, amazement</p>

        
        <input class="slider" type="range" id="sadness" min="0" max="100" step="1" v-model="movie1.Sadness" />
        <p><label for="sadness">Sadness {{movie1.Sadness}}</label> To connect us with those we love - pensiveness, sadness, grief</p>
        
        <input class="slider" type="range" id="disgust" min="0" max="100" step="1" v-model="movie1.Disgust" />                
        <p><label for="disgust">Disgust {{movie1.Disgust}}</label> To reject what is unhealthy - boredom, disgust, loathing</p>
      <button @click="submitMoods(movie1)">Submit Moods</button>
      </div>
    </div>


  </div>
</template>

<script>

import Poster from './../components/Poster'
import NavAdmin from './../components/NavAdmin'

import MovieService from './../MovieService'

export default {
  name: 'MovieMoods',
  data() {
    return {
      moodImput: 'One',
      mood: 'Anger',
      movie1: {
        Anger: 50,
        Anticipation: 50,
        Joy: 50,
        Trust: 50,
        Fear: 50,
        Surprise: 50,
        Sadness: 50,
        Disgust: 50,
      },
      movie2: null,

    }
  },
  components: {
    Poster,
    NavAdmin,
  },
  created() {
    if (this.$route.params.id) {
      this.getMovieData(this.$route.params.id)
    } else {
      this.getRandomMovie1()
    }
    this.getRandomMovie2()


  },
  methods: {
    async getRandomMovie1() {
       this.movie1 = await MovieService.getRandomMovie()
    },
    async getRandomMovie2() {
       this.movie2 = await MovieService.getRandomMovie()
    },
    async getMovieData(id) {
       this.movie1 = await MovieService.getOneMovie(id)
    },
    async submitMoods(movie) {
      await MovieService.updateMovieMoods(movie)
      this.getRandomMovie1()
    }
    
  }
}
</script>

