import axios from 'axios';

const url = '/api/primeid/'

class PrimeidService {

    //Get Shapes
    static getPrimeIds(query) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}`, {
                    params: {
                        start: query.start,
                        count: query.count,
                    }
                });
                const data = res.data;
                resolve(data);
            } catch (err) {
                reject(err);
            }
        });
    }
}


export default PrimeidService;