import axios from 'axios';

const url = '/api/windows/'

class WindowService {

    //Get Windows
    static getWindows() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(windows => ({
                        ...windows,
                        createdAt: new Date(windows.createdAt)
                    }))
                );
            } catch (err) {
                reject(err);
            }
        });
    }
    
    //Get One Window
    static getOneWindow(id) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}${id}`)
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err);
            }
        });
    }

    // Create Window
    static insertWindow(window) {
        return axios.post(url, {
            name: window.name,
            ids: window.ids,
        });
    }

    //Update Window
    static updateWindow(window) {
        const id = window._id
        return axios.put(`${url}/${id}`, {
            name: window.name,
            ids: window.ids,
        });
    }

    // Delete Window

    static deleteWindow(id) {
        return axios.delete(`${url}/${id}`);
    }
}

export default WindowService;

