<template>
<div>
  <nav-admin />
  <div class="moodSliders">
    <select @change="setMood($event)">
      <option>I'm in the mood2watch something...</option>
      <option
      v-for="(word, index) in allWords"
      :key="word._id"
      :value="index"
      >{{word.word}}</option>
    </select>

        
    <input class="slider" type="range" id="anger" min="0" max="100" step="1" v-model="anger" />
    <p><label for="anger">Anger {{anger}}</label> To fight against problems - annoyance, anger, rage</p>
    
    <input class="slider" type="range" id="anticipation" min="0" max="100" step="1" v-model="anticipation" />        
    <p><label for="anticipation">Anticipation {{anticipation}}</label> To look forward to a plan - interest, anticipation, vigilance</p>

    <input class="slider" type="range" id="joy" min="0" max="100" step="1" v-model="joy" />
    <p><label for="joy">Joy {{joy}}</label> To remind us what's important - serenity, joy, ecstasy</p>

    <input class="slider" type="range" id="trust" min="0" max="100" step="1" v-model="trust" />
    <p><label for="trust">Trust {{trust}}</label> To connect with peolpe that help - acceptance, trust, admiration</p>
    
    <input class="slider" type="range" id="fear" min="0" max="100" step="1" v-model="fear" />
    <p><label for="fear">Fear {{fear}}</label> To protect us from danger - apprehension, fear, terror</p>

    <input class="slider" type="range" id="surprise" min="0" max="100" step="1" v-model="surprise" />
    <p><label for="surprise">Surprise {{surprise}}</label> To focus us on new situations - distraction, surprise, amazement</p>

    
    <input class="slider" type="range" id="sadness" min="0" max="100" step="1" v-model="sadness" />
    <p><label for="sadness">Sadness {{sadness}}</label> To connect us with those we love - pensiveness, sadness, grief</p>
    
    <input class="slider" type="range" id="disgust" min="0" max="100" step="1" v-model="disgust" />                
    <p><label for="disgust">Disgust {{disgust}}</label> To reject what is unhealthy - boredom, disgust, loathing</p>
    
    <button @click="getMoviesByMood">Submit Moods</button>
  </div>

  <div
  class="mood-result-list"
  v-for="movie in moodList"
  :key="movie.doc._id">
    <router-link :to="'/moods/' + movie.doc._id">
      <movie
      class="movie"
      :movie="movie.doc" />
    </router-link>
  </div>


</div>
</template>

<script>
// @ is an alias to /src

import MovieService from './../MovieService'
import WordService from './../WordService'

import NavAdmin from './../components/NavAdmin.vue'
import Movie from './../components/Movie.vue'

export default {
  name: 'play',
  data() {
    return {
      movieList: null,
      moodList: null,
      year: 2010,
      count: 12,
      page: 1,
      mood: 'Anger',
      anger: 50,
      anticipation: 50,
      joy: 50,
      trust: 50,
      fear: 50,
      surprise: 50,
      sadness: 50,
      disgust: 50,
      allWords: [],
      wordCount: 100,
      wordPage: 1,
      selected: null,
    }
  },
    components: {
      Movie,
      NavAdmin,
  },
  created() {
    this.getMoviesByMood()
    this.getWords()
  },
  methods: {

    async getMoviesByMood() {
      this.moodList = await MovieService.getMoviesByMood({
        count: this.count,
        page: this.page,

        anger: this.anger,
        anticipation: this.anticipation,
        joy: this.joy,
        trust: this.trust,
        fear: this.fear,
        surprise: this.surprise,
        sadness: this.sadness,
        disgust: this.disgust,
      })
    },
    async getWords() {
      this.allWords = await WordService.getWords({
          count: this.wordCount,
          page: this.wordPage,
      })
    },
    setMood(event) {
      this.selected = event.target.value
      this.anger = this.allWords[this.selected].Anger
      this.anticipation = this.allWords[this.selected].Anticipation
      this.joy = this.allWords[this.selected].Joy
      this.trust = this.allWords[this.selected].Trust
      this.fear = this.allWords[this.selected].Fear
      this.surprise = this.allWords[this.selected].Surprise
      this.sadness = this.allWords[this.selected].Sadness
      this.disgust = this.allWords[this.selected].Disgust
      this.getMoviesByMood()
    }
    
  },
}
</script>

