const axios = require('axios');


const url = '/api/poster/'

class GetPosterService {

    // Get Poster from Omdb
    static getPoster(payload) {

        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.post(url, {
                    poster: payload.poster,
                    id: payload.id,
                });
                const moodPoster = res.data;
                resolve(moodPoster)
            } catch (err) {
                reject(err)
            }
        });
    }

}

export default GetPosterService;