import axios from 'axios';

const url = '/api/words/'

class WordService {

    //Get Words
    static getWords(query) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url, {
                    params: {
                        count: query.count,
                        page: query.page,
                    }
                });
                const data = res.data;
                resolve(data);
            } catch (err) {
                reject(err);
            }
        });
    }
    
    //Get One Word
    static getOneWord(id) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}${id}`)
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err);
            }
        });
    }

    // Create Word
    static insertWord(word) {
        return axios.post(url, {
            word: word.word,
            Anger: word.Anger,
            Anticipation: word.Anticipation,
            Joy: word.Joy,
            Trust: word.Trust,
            Fear: word.Fear,
            Surprise: word.Surprise,
            Sadness: word.Sadness,
            Disgust: word.Disgust,

        });
    }

    //Add Many Words
    static addManyWords(objArray) {
        return axios.post(`${url}/many`, objArray); 
    }

    //Update Word
    static updateWord(word) {
        const id = word._id
        return axios.put(`${url}${id}`, {
            word: word.word,
            Anger: word.Anger,
            Anticipation: word.Anticipation,
            Joy: word.Joy,
            Trust: word.Trust,
            Fear: word.Fear,
            Surprise: word.Surprise,
            Sadness: word.Sadness,
            Disgust: word.Disgust,
        });
    }

    // Delete Word
    static deleteWord(id) {
        return axios.delete(`${url}/${id}`);
    }
}

export default WordService;

