<template>
  <div class="container">
    <nav-admin />

    <h1>Word Moods</h1>

    <div>
      <h3>Add Words</h3>
      <label for="word">Word</label>
      <input id="word" type="text" v-model="addWord" />
      <label for="Anger">Anger</label>
      <input id="Anger" type="number" v-model="Anger" />
      <label for="Anticipation">Anticipation</label>
      <input id="Anticipation" type="number" v-model="Anticipation" />
      <label for="Joy">Joy</label>
      <input id="Joy" type="number" v-model="Joy" />
      <label for="Trust">Trust</label>
      <input id="Trust" type="number" v-model="Trust" />
      <label for="Fear">Fear</label>
      <input id="Fear" type="number" v-model="Fear" />
      <label for="Surprise">Surprise</label>
      <input id="Surprise" type="number" v-model="Surprise" />
      <label for="Sadness">Sadness</label>
      <input id="Sadness" type="number" v-model="Sadness" />
      <label for="Disgust">Disgust</label>
      <input id="Disgust" type="number" v-model="Disgust" />
      <button @click="addNewWord">Add Word</button>
    </div>


<br />

    <table>
      <tr>
        <th>ID</th>
        <th>Word</th>
        <th>Anger</th>
        <th>Anticipation</th>
        <th>Joy</th>
        <th>Trust</th>
        <th>Fear</th>
        <th>Surprise</th>
        <th>Sadness</th>
        <th>Disgust</th>
      </tr>
      <tr
      v-for="word in allWords"
      :key="word._id">
        <td class="id">
            <span>{{word._id}}</span>
        </td>
        <td class="word">
            <span v-if="edit !== word._id" >{{word.word}}</span>
            <input v-if="edit === word._id" type="text" v-model="word.word"/>
        </td>
        <td class="mood">
            <span v-if="edit !== word._id" >{{word.Anger}}</span>
            <input v-if="edit === word._id" type="number" v-model="word.Anger"/>
        </td>
        <td class="mood">
            <span v-if="edit !== word._id" >{{word.Anticipation}}</span>
            <input v-if="edit === word._id" type="number" v-model="word.Anticipation"/>
        </td>
        <td class="mood">
            <span v-if="edit !== word._id" >{{word.Joy}}</span>
            <input v-if="edit === word._id" type="number" v-model="word.Joy"/>
        </td>
        <td class="mood">
            <span v-if="edit !== word._id" >{{word.Trust}}</span>
            <input v-if="edit === word._id" type="number" v-model="word.Trust"/>
        </td>
        <td class="mood">
            <span v-if="edit !== word._id" >{{word.Fear}}</span>
            <input v-if="edit === word._id" type="number" v-model="word.Fear"/>
        </td>
        <td class="mood">
            <span v-if="edit !== word._id" >{{word.Surprise}}</span>
            <input v-if="edit === word._id" type="number" v-model="word.Surprise"/>
        </td>
        <td class="mood">
            <span v-if="edit !== word._id" >{{word.Sadness}}</span>
            <input v-if="edit === word._id" type="number" v-model="word.Sadness"/>
        </td>
        <td class="mood">
            <span v-if="edit !== word._id" >{{word.Disgust}}</span>
            <input v-if="edit === word._id" type="number" v-model="word.Disgust"/>
        </td>
        <td>
            <a v-if="edit !== word._id" @click="edit = word._id">edit</a>
            <a
            v-if="edit === word._id"
            @click="updateWord(word)">update</a><br />
            <a v-if="edit === word._id" @click="edit = false">cancel</a><br />
            <a v-if="edit === word._id" @click="deleteWord(word._id)">delete</a>
            
        </td>
      </tr>
    </table>
  </div>
</template>

<script>

import NavAdmin from './../components/NavAdmin'

import WordService from './../WordService'

export default {
  name: 'Words',
  data() {
    return {
        edit: false,
        addWord: '',
        word: '',
        Anger: 50,
        Anticipation: 50,
        Joy: 50,
        Trust: 50,
        Fear: 50,
        Surprise: 50,
        Sadness: 50,
        Disgust: 50,
        count: 100,
        page: 1,
        allWords: null,
    }
  },
  components: {
    NavAdmin,
  },
  created() {
      this.getWords()
  },
  methods: {
    async addNewWord() {
        await WordService.insertWord({
        word: this.addWord,
        Anger: this.Anger,
        Anticipation: this.Anticipation,
        Joy: this.Joy,
        Trust: this.Trust,
        Fear: this.Fear,
        Surprise: this.Surprise,
        Sadness: this.Sadness,
        Disgust: this.Disgust,
        }),
        this.getWords()
    },
    async getWords() {
        this.allWords = await WordService.getWords({
            count: this.count,
            page: this.page,
        })
        this.edit = ''
    },
    
    async updateWord(word) {
      await WordService.updateWord(word)
      this.getWords()
    },

    async deleteWord(id) {
      await WordService.deleteWord(id)
      this.getWords()
    },

  },
}
</script>

