<template>
<div>
  <div id="mood-select-container">
    <div
    @mousemove="setMouse"
    @mouseleave="hover = false"
    class="mood-hot-area">
      
        <div
        class="moodPosition"
        v-if="hover"
        @click="getMoviesByWord"
        :style="'top: +' + (this.mouseY - 38) + 'px; left:' + (this.mouseX - 20) + 'px;'">
          <svg height="140" width="500">
            <ellipse cx="20" cy="38" rx="14" ry="14" style="fill: #ffffff50; stroke: #ffffff; stroke-width:5" />
              <text x="44" y="46" fill="#ffffff" style="font-family:Verdana, sans-serif; font-size: 24px; color: #ddddddb0">{{ displayMoodWord }}</text>
          </svg>
        </div>

    </div>
  </div>
  <div class="logo"><p>mood2watch</p></div>
  
  <div id="movie-list-container">
    <div class="headline">
      <h3>I'm in the mood to watch something <strong><i>{{ mood.word }}</i></strong></h3>
    </div>
    <div class="less-more" v-if="moodList">
      <button class="less" @click="getMoviesByMood(.9)"> less {{ mood.word }}</button>
      <button class="more" @click="getMoviesByMood(1.1)">more {{ mood.word }} </button> 
    </div>
    <div v-if="moodList">
      <div
      class="mood-result-list"
      v-for="movie in moodList"
      :key="movie.doc._id">
      <router-link :to="'/admin/moods/' + movie.doc._id">
        <movie
        class="movie"
        :movie="movie.doc" />
      </router-link>
      </div> 
    </div>
  </div>

  

</div>
</template>

<script>

import MovieService from './../MovieService'
import WordService from './../WordService'

import Movie from './../components/Movie.vue'

export default {
  name: 'home',
  data() {
    return {
      hover: false,
      mouseX: 0,
      mouseY: 0,
      moodIndex: 0,
      movieList: null,
      moodList: null,
      year: 2010,
      count: 16,
      page: 1,
      displayMoodWord: '...',
      mood: {
        word: '...',
      },
      anger: 50,
      anticipation: 50,
      joy: null,
      trust: 50,
      fear: 50,
      surprise: 50,
      sadness: 50,
      disgust: 50,
      allWords: [],
      wordCount: 100,
      wordPage: 1,
      selected: null,
    }
  },
    components: {
      Movie,
  },
  created() {
    this.getWords()
  },

  methods: {

    setMouse(e) {
      this.mouseY = e.clientY
      this.mouseX = e.clientX
      this.hover = true

      if (e.clientY > 400) {
        this.hover = false
      }

      let windowWidth = window.innerWidth
      // let moodCount = this.allWords.length
      let columns = 9
      let rows = 3
      
      this.moodRow =  Math.round(rows / (400 / e.clientY))
      this.moodColumn = Math.round(columns / (windowWidth / e.clientX))
      this.moodIndex = (this.moodRow * 9) + this.moodColumn
      this.displayMoodWord = this.allWords[this.moodIndex].word
    },

    async getMoviesByWord() {
      this.mood = this.allWords[this.moodIndex]
      // this.displayMoodWord = this.mood.word
      this.moodList = await MovieService.getMoviesByWord({
        count: this.count,
        page: this.page,
        word: this.mood.word,
      })
    },

    async getMoviesByMood(adjust) {

      this.mood.Anger = Math.round(Number(this.mood.Anger) * adjust),
      this.mood.Anticipation = Math.round(Number(this.mood.Anticipation) * adjust),
      this.mood.Joy = Math.round(Number(this.mood.Joy) * adjust),
      this.mood.Trust = Math.round(Number(this.mood.Trust) * adjust),
      this.mood.Fear = Math.round(Number(this.mood.Fear) * adjust),
      this.mood.Surprise = Math.round(Number(this.mood.Surprise) * adjust),
      this.mood.Sadness = Math.round(Number(this.mood.Sadness) * adjust),
      this.mood.Disgust = Math.round(Number(this.mood.Disgust) * adjust),


      this.moodList = await MovieService.getMoviesByMood({
        count: this.count,
        page: this.page,

        anger: this.mood.Anger,
        anticipation: this.mood.Anticipation,
        joy: this.mood.Joy,
        trust: this.mood.Trust,
        fear: this.mood.Fear,
        surprise: this.mood.Surprise,
        sadness: this.mood.Sadness,
        disgust: this.mood.Disgust,
      })
    },

    async getWords() {
      this.allWords = await WordService.getWords({
          count: this.wordCount,
          page: this.wordPage,
      })
    },
    
  },
}
</script>

