import axios from 'axios';

const url = '/api/movies/'

class MovieService {

    //Get Movies
    static getMovies(query) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}`, {
                    params: {
                        by: query.by,
                        year: query.year,
                        count: query.count,
                        page: query.page,
                        imdbId: query.imdbId,
                        title: query.title,
                        sort: query.sort,
                    }
                });
                const data = res.data;
                resolve(
                    data.map(movies => ({
                        ...movies,
                        createdAt: new Date(movies.createdAt)
                    }))
                );
            } catch (err) {
                reject(err);
            }
        });
    }

    static getMoviesByMood(query)  {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url, {
                    params: {
                        by: 'mood',
                        count: query.count,
                        page: query.page,

                        anger: query.anger,
                        anticipation: query.anticipation,
                        joy: query.joy,
                        trust: query.trust,
                        fear: query.fear,
                        surprise: query.surprise,
                        sadness: query.sadness,
                        disgust: query.disgust,
                    }
                });
                const data = res.data;
                resolve(data);
            } catch (err) {
                reject(err);
            }
        });
    }

    static getMoviesByWord(query)  {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url, {
                    params: {
                        by: 'word',
                        count: query.count,
                        page: query.page,
                        word: query.word,
                    }
                });
                const data = res.data;
                resolve(data);
            } catch (err) {
                reject(err);
            }
        });
    }
    
    //Get One Movie
    static getOneMovie(id) {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}${id}`)
                const data = res.data
                resolve(data)
            } catch (err) {
                reject(err);
            }
        });
    }


    static getRandomMovie() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(`${url}`, {
                    params: {
                        by: 'random',
                    }
                });
                const data = res.data
                resolve(data[0]);

            } catch (err) {
                reject(err);
            }
        });
    }

    // Create Movie
    static insertMovie(newMovie) {
        return axios.post(url, {
            imdbId: newMovie.imdbId,
            Actors: newMovie.Actors,
            Awards: newMovie.Awards,
            BoxOffice: newMovie.BoxOffice,
            Country: newMovie.Country,
            DVD: newMovie.DVD,
            Director: newMovie.Director,
            Genre: newMovie.Genre,
            Language: newMovie.Language,
            Metascore: newMovie.Metascore,
            Plot: newMovie.Plot,
            Poster: newMovie.Poster,
            Production: newMovie.Production,
            Rated: newMovie.Rated,
            Ratings: newMovie.Ratings,
            Released: newMovie.Released,
            Response: newMovie.Response,
            Runtime: newMovie.Runtime,
            Title: newMovie.Title,
            Type: newMovie.Type,
            Website: newMovie.Website,
            Writer: newMovie.Writer,
            Year: newMovie.Year,
            imdbRating: newMovie.imdbRating,

        });
    }

    //Add Many Movies
    static addManyMovies(objArray) {
        return axios.post(`${url}/many`, objArray); 
    }

    //Update Movie
    static updateMovie(movie) {
        const id = movie._id
        return axios.put(`${url}/${id}`, {
            imdbRating: movie.imdbRating,
            imdbID: movie.imdbID,
            Poster: movie.Poster,
            moodPoster: movie.moodPoster,
            Anger: movie.Anger,
            Anticipation: movie.Anticipation,
            Joy: movie.Joy,
            Trust: movie.Trust,
            Fear: movie.Fear,
            Surprise: movie.Surprise,
            Sadness: movie.Sadness,
            Disgust: movie.Disgust,
        });
    }

    //Update Movie
    static updateMovieMoods(movie) {
        const id = movie._id
        return axios.put(`${url}/${id}`, {
            Anger: movie.Anger,
            Anticipation: movie.Anticipation,
            Joy: movie.Joy,
            Trust: movie.Trust,
            Fear: movie.Fear,
            Surprise: movie.Surprise,
            Sadness: movie.Sadness,
            Disgust: movie.Disgust,
        });
    }

    //Update Movie
    static updateMoviePoster(payload) {
        return axios.put(`${url}/${payload.id}`, {
            moodPoster: payload.poster,  
        });
    }

    // Delete Movie

    static deleteMovie(id) {
        return axios.delete(`${url}/${id}`);
    }
}

export default MovieService;

