<template>
  <div class="container">
    <nav-admin />

    <h1>Available on Amazon Prime</h1>

    <div>
      <h3>Get Prime IDs</h3>
      <label for="imdbStart">start</label>
      <input id="imdbStart" type="text" v-model="imdbStart" />
      <label for="imdbCount">count</label>
      <input id="imdbCount" type="text" v-model="imdbCount" />
      <button @click="scrapePrimeIds">Scrape</button>
    </div>


<br />

    <table>
      <tr>
        <th>ID</th>
        <th>Window Name</th>
        <th>Count</th>
        <th>IDs Array</th>
        <th>Edit</th>
      </tr>
      <tr>
        <td></td>
        <td><input type="text" v-model='window.name' /></td>
        <td><span>{{window.count}}</span></td>
        <td><div class="ids"><span>{{window.ids}}</span></div></td>
        <td>
          <button @click="createWindow">create</button>
        </td>
      </tr>
      <tr
      v-for="window in allWindows"
      :key="window._id">
        <td><span>{{window._id}}</span></td>
        <td><span>{{window.name}}</span></td>
        <td><span>{{window.ids.length}}</span></td>
        <td><div class="ids"><span>{{window.ids}}</span></div></td>
        <td></td>
      </tr>
    </table>
  </div>
</template>

<script>
import NavAdmin from './../components/NavAdmin'

import PrimeidService from './../PrimeidService'
import WindowService from './../WindowService'

export default {
  name: 'PrimeIds',
  data() {
    return {
      window: {
        name: 'prime',
        ids: [],
        count: 0,
      },
      imdbStart: 1,
      imdbCount: 250,
      resultIds: [],
      allWindows: null,
    }
  },
  components: {
    NavAdmin,
  },
  created() {
    this.getWindows()

  },
  methods: {
    async scrapePrimeIds() {
      this.resultIds = await PrimeidService.getPrimeIds({
        start: this.imdbStart,
        count: this.imdbCount,
      })
      let arrayLength = this.resultIds.length;
      for (var i = 0; i < arrayLength; i++) {
          let id = this.resultIds[i]
           if ( !this.window.ids.includes(id) ) {
               this.window.ids.push(id)
           }
      }
      this.window.count = this.window.ids.length
    },
    async createWindow() {
      await WindowService.insertWindow(this.window)
      this.getWindows()
    },
    async getWindows() {
      this.allWindows = await WindowService.getWindows()
    }
  }
}
</script>

